* {
  font-family: "Times New Roman";
}
body {
  padding: 60px;
}
input {
  color: black;
  font-size: 1.2rem;
  width: 100%;
}
.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}
.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.animate__slideInDow {
  animation-duration: 5s; 
}
